	      	handleEdit(index, row) {
	        	this.$prompt('请输入修改地址', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消'
		        }).then(({ value }) => {
		        	row.address = value;
		          	this.$message({
		            	type: 'success',
		            	message: '修改成功'
		          	});
		        }).catch(() => {
		          	this.$message({
		            	type: 'info',
		            	message: '取消修改'
		          	});
		        });
	      	},
	      	handleDelete(index, row) {
	        	this.$confirm('此操作将删除该内容, 是否继续?', '提示', {
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消',
		          	type: 'warning'
		        }).then(() => {
		        	this.tableData.splice(index, 1);
		          	this.$message({
		            	type: 'success',
		           	 	message: '删除成功!'
		          	});
		        }).catch(() => {
		          	this.$message({
		            	type: 'info',
		            	message: '已取消删除'
		          	});
		        });
	      	}
	    }
	}
</script>

<style>
	.system .el-table{
		width: 98%;
    	margin: 15px auto;
	   	box-shadow: 1px 1px 1px 1px #DCDCDC;
	}	
  	.el-table__body-wrapper {
	    overflow: hidden;
	}
</style>


