import Vue from 'vue'
import Router from 'vue-router'
import system from '@/components/page/system'
import Visitor from '@/components/page/Visitor'
import Statistics from '@/components/page/Statistics'
import Analysis from '@/components/page/Analysis'
import setUp from '@/components/page/setUp'

Vue.use(Router)

export default new Router({
	mode: 'history',
  	routes: [
	    {
	      	path: '/',
	      	name: 'syetem',
	      	component: system
	    },{
	      	path: '/Visitor',
	      	name: 'Visitor',
	      	component: Visitor    	
	    },{
	      	path: '/Statistics',
	      	name: 'Statistics',
	      	component: Statistics    	
	    },{
	      	path: '/Analysis',
	      	name: 'Analysis',
	      	component: Analysis    	
	    },{
	      	path: '/setUp',
	      	name: 'setUp',
	      	component: setUp    	
	    }
  	]
})



