<template>
	<div class="setUp">
      <div class="setup_contaion">
         <el-form ref="form" :model="form" label-width="80px">
           <el-form-item label="模块名称">
             <el-input v-model="form.name"></el-input>
           </el-form-item>
           <el-form-item label="模块区域">
             <el-input v-model="form.password"></el-input>
           </el-form-item>
           <el-form-item label="添加时间">
             <el-col :span="11">
               <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
             </el-col>
             <el-col class="line" :span="2">-</el-col>
             <el-col :span="11">
               <el-time-picker type="fixed-time" placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
             </el-col>
           </el-form-item>
           <el-form-item label="模块性质">
             <el-checkbox-group v-model="form.type">
               <el-checkbox label="仅自己可见" name="type"></el-checkbox>
               <el-checkbox label="指定人可见" name="type"></el-checkbox>
               <el-checkbox label="所有人可见" name="type"></el-checkbox>
               <el-checkbox label="开放部分" name="type"></el-checkbox>
             </el-checkbox-group>
           </el-form-item>
           <el-form-item label="是否开放">
             <el-radio-group v-model="form.resource">
               <el-radio label="开放"></el-radio>
               <el-radio label="不开放"></el-radio>
             </el-radio-group>
           </el-form-item>
           <el-form-item label="详细描述">
             <el-input type="textarea" v-model="form.desc"></el-input>
           </el-form-item>
           <el-form-item>
             <el-button type="primary" @click="onSubmit">立即创建</el-button>
             <el-button>取消</el-button>
           </el-form-item>
         </el-form>
      </div>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          password: '',
          date1: '',
          date2: '',
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style>
   .setup_contaion {
      width: 98%;
      margin: 25px auto;
      background: #fff;
      border: 1px solid #d1dbe5;
      padding: 45px 0;
   }
   .setup_contaion .el-form {
       width: 440px;
       margin: 0 auto;
   }
   .el-select .el-input {
       width: 360px;
   }
   .el-checkbox-group .el-checkbox {
      float: left;
       width: 160px;
       padding-right: 20px;
       margin: 0;
       padding: 0;
   }
   .el-checkbox {
       color: #1f2d3d;
       position: relative;
       text-align: left;
       cursor: pointer;
       display: inline-block;
       white-space: nowrap;
       -moz-user-select: none;
       -webkit-user-select: none;
       -ms-user-select: none;
   }
   .el-radio-group {
      position: relative;
      top: 8px;
      float: left;
   }
</style>


