<template>
	<div class="Analysis">
      <div class="analysis_contaion">
         <div class="analysis_infor">
		       <el-button type="text" @click="open"><i class="el-icon-view"></i>点击查看</el-button>
         </div>
         <template>
           <el-table
             :data="tableData"
             border
             style="width: 100%"
             :default-sort = "{prop: 'date', order: 'descending'}"
             >
             <el-table-column
               prop="date"
               label="日期"
               sortable
               width="180">
             </el-table-column>
             <el-table-column
               prop="rate"
               label="转化率"
               sortable
               width="180">
             </el-table-column>
             <el-table-column
               prop="describe"
               label="转化描述"
               :formatter="formatter">
             </el-table-column>
           </el-table>
         </template>
      </div>
	</div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2017-05-02',
          rate: '97%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '2017-05-04',
          rate: '91%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '201705-01',
          rate: '89%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '2017-05-03',
          rate: '96%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '2017-05-04',
          rate: '91%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '201705-01',
          rate: '89%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }, {
          date: '2017-05-03',
          rate: '96%',
          describe: '网页访问时长、访问量及只用情况比较优良。'
        }]
      }
    },
    methods: {
      open() {
        this.$alert('该后网站由Vue.js、element组件及ECharts混合开发完成。', '分析内容', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `明白了！`
            });
          }
        });
      },
      formatter(row, column) {
        return row.describe;
      }
    }
  }
</script>

<style>
   .Analysis .analysis_contaion{
      width: 98%;
      margin: 25px auto;
      background: #fff;
      border: 1px solid #d1dbe5;
   }
   .analysis_infor .el-button{
      float: right;
      padding: 15px 15px;
   }
</style>


