<template>
	<div class="vistor">
		<el-row class="visitor_head">
		  	<el-col :span="24">
			  	<div class="grid-content bg-purple-dark">
			  		<h5 class="title">今日访客</h5>
					<template>
					  	<el-table :data="tableData" border style="width: 100%">
						    <el-table-column prop="date" label="日期"  width="180"></el-table-column>
						    <el-table-column prop="flow" label="流量数" width="180"></el-table-column>
						    <el-table-column prop="caller" label="访客数"></el-table-column>
						    <el-table-column prop="ip" label="IP数"></el-table-column>
						    <el-table-column prop="time" label="访问时长(小时)"></el-table-column>
						    <el-table-column prop="conversion" label="转化率"></el-table-column>
					  	</el-table>
					</template>
			  	</div>
		  	</el-col>
		</el-row>
		<el-row class="visitor_main">
		  	<el-col class="visitor_fl" :span="12">
			  	<div class="grid-content bg-purple sizeParent">
					<div id="main1"  :style="{width:'800px',height:'400px'}"></div>
			  	</div>
		  	</el-col>
		  	<el-col class="visitor_fr" :span="12">
		  		<div class="grid-content bg-purple-light sizeParent">
		  			<div id="main2"  :style="{width:'800px',height:'400px'}"></div>
		  		</div>
		  	</el-col>
		</el-row>
	</div>
</template>


<script>

// echarts相关
let echarts = require('echarts/lib/echarts');
require('echarts/map/js/china.js');
require('echarts/map/json/china.json');

 	export default {
		data() {
	      	return {
	        tableData: [{
		          date: '2017-05-02',
		          flow: '5423',
		          caller: '12345',
		          ip: '1312389732101012',
		          time: '98',
		          conversion: '90%'
		        }, {
		          date: '2016-05-04',
		          flow: '7823',
		          caller: '34345',
		          ip: '93434523423524234234',
		          time: '78',
		          conversion: '78%'
		        }, {
		          date: '2015-05-01',
		          flow: '3423',
		          caller: '345345',
		          ip: '74586788574789634',
		          time: '67',
		          conversion: '56%'
		        }, {
		          date: '2014-05-03',
		          flow: '2323',
		          caller: '5645',
		          ip: '21475679734356745',
		          time: '123',
		          conversion: '23%'
		        }]
      		}
    	},
 	   	mounted (){
 	   		var main1 = document.getElementById('main1');
 	   		var main2 = document.getElementById('main2');
 	   		var imgSize = function() {
 	   			var canvas_w = document.getElementsByClassName('grid-content')[1].clientWidth-30;
 	   			main1.style.width = canvas_w +'px';
 	   			main2.style.width = canvas_w +'px';
 	   		}
 	   		imgSize();
         	var myChart1 = echarts.init(main1);
         	var myChart2 = echarts.init(main2);
			// 绘制图表
	        var option1 = {
	            tooltip: {
	               show: true
	            },
	            legend: {
	               data:['销量']
	            },
	            toolbox: {
				      show : true,
				      feature : {
				         mark : {show: true},
				         dataView : {show: true, readOnly: false},
				         magicType : {show: true, type: ['line', 'bar']},
				         restore : {show: true},
				         saveAsImage : {show: true}
				      }
				   },
	            xAxis : [
	               {
	                  type : 'category',
	                  boundaryGap : false,
	            		data : ['周一','周二','周三','周四','周五','周六','周日']
	               }
	            ],
	            yAxis : [
	               {
	                  type : 'value',
	                  axisLabel : {
			               formatter: '{value} 万次'
			            }
	               }
	            ],
	            series : [
	               {
		            	name:'白天访问量',
		            	type:'line',
		            	data:[14.5, 15, 13, 14, 12, 12, 10],
		            	markPoint : {
		                	data : [
		                    	{type : 'max', name: '最大值'},
		                    	{type : 'min', name: '最小值'}
		                	]
			            },
			            markLine : {
		               	data : [
		                    	{type : 'average', name: '平均值'}
		                	]
		            	}
		            },{
			            name:'夜间访问量',
			            type:'line',
			            data:[1, 1.5, 2, 3, 3, 5, 6.5],
			            markPoint : {
			               data : [
			                  {name : '访问均值', value : 3, xAxis: 3, yAxis: 3}
			               ]
			            },
			            markLine : {
			               data : [
			                  {type : 'average', name : '平均值'}
			               ]
			            }
			        }
	            ]
	        };
			var option2 = {
			    title : {
			        text: '访问量',
			        subtext: '各省市分布',
			        x: 'right'
			    },
			    dataRange: {
			        x: 'left',
			        y: 'bottom',
			        splitList: [
			            {start: 1500},
			            {start: 900, end: 1500},
			            {start: 310, end: 1000},
			            {start: 200, end: 300},
			            {start: 10, end: 200, label: '10 到 200（自定义label）'},
			            {start: 5, end: 5, label: '5（自定义特殊颜色）', color: 'black'},
			            {end: 10}
			        ],
			        color: ['#8dd0ed', '#d5ee55', '#ffb900']
			    },
			    toolbox: {
			        show: true,
			        orient : 'vertical',
			        x: 'right',
			        y: 'center',
			        feature : {
			            mark : {show: true},
			            dataView : {show: true, readOnly: false},
			            restore : {show: true},
			            saveAsImage : {show: true}
			        }
			    },
			    roamController: {
			        show: true,
			        x: 'right',
			        mapTypeControl: {
			            'china': true
			        }
			    },
			    series : [
			        {
			            name: '订单量',
			            type: 'map',
			            mapType: 'china',
			            roam: false,
			            itemStyle:{
			                normal:{
			                    label:{
			                        show:true,
			                        textStyle: {
			                           color: "rgb(249, 249, 249)"
			                        }
			                    }
			                },
			                emphasis:{label:{show:true}}
			            },
			            data:[
			                {name: '北京',value: Math.round(Math.random()*2000)},
			                {name: '天津',value: Math.round(Math.random()*2000)},
			                {name: '上海',value: Math.round(Math.random()*2000)},
			                {name: '重庆',value: Math.round(Math.random()*2000)},
			                {name: '河北',value: 0},
			                {name: '河南',value: Math.round(Math.random()*2000)},
			                {name: '云南',value: 5},
			                {name: '辽宁',value: 305},
			                {name: '黑龙江',value: Math.round(Math.random()*2000)},
			                {name: '湖南',value: 200},
			                {name: '安徽',value: Math.round(Math.random()*2000)},
			                {name: '山东',value: Math.round(Math.random()*2000)},
			                {name: '新疆',value: Math.round(Math.random()*2000)},
			                {name: '江苏',value: Math.round(Math.random()*2000)},
			                {name: '浙江',value: Math.round(Math.random()*2000)},
			                {name: '江西',value: Math.round(Math.random()*2000)},
			                {name: '湖北',value: Math.round(Math.random()*2000)},
			                {name: '广西',value: Math.round(Math.random()*2000)},
			                {name: '甘肃',value: Math.round(Math.random()*2000)},
			                {name: '山西',value: Math.round(Math.random()*2000)},
			                {name: '内蒙古',value: Math.round(Math.random()*2000)},
			                {name: '陕西',value: Math.round(Math.random()*2000)},
			                {name: '吉林',value: Math.round(Math.random()*2000)},
			                {name: '福建',value: Math.round(Math.random()*2000)},
			                {name: '贵州',value: Math.round(Math.random()*2000)},
			                {name: '广东',value: Math.round(Math.random()*2000)},
			                {name: '青海',value: Math.round(Math.random()*2000)},
			                {name: '西藏',value: Math.round(Math.random()*2000)},
			                {name: '四川',value: Math.round(Math.random()*2000)},
			                {name: '宁夏',value: Math.round(Math.random()*2000)},
			                {name: '海南',value: Math.round(Math.random()*2000)},
			                {name: '台湾',value: Math.round(Math.random()*2000)},
			                {name: '香港',value: Math.round(Math.random()*2000)},
			                {name: '澳门',value: Math.round(Math.random()*2000)}
			            ]
			        }
			    ]
			};
         	// 为echarts对象加载数据
         	myChart1.setOption(option1);
         	myChart2.setOption(option2);
			window.onresize = function () {
			    //重置容器宽 
			    imgSize();
			    myChart1.resize();
			    myChart2.resize();
			};          
     	}
  	}




</script>

<style>
	.visitor_head {
	   	background: #fff;
	   	border: 1px solid #efefef;
	   	margin: 12px;
	   	padding: 15px;
	   	box-shadow: 1px 1px 1px 1px #DCDCDC;
	}
	.visitor_main .grid-content {
		margin: 0 12px;
		padding: 15px;
	   	background: #fff;
	   	border: 1px solid #efefef;
	   	box-shadow: 1px 1px 1px 1px #DCDCDC;
	}
	.title {
		text-align: left;
    	margin: 10px 0;
	}
	.el-table th {
		text-align: center;
	}
	.el-table td, .el-table th.is-leaf {
    	border-bottom:0;
  	}
  	.el-table th>.cell {
    	color: #5b5d61;
    	font-weight: 100;
  	}
</style>


