<template>
  <div id="app">
    <headColumn></headColumn>
    <div class="container">
        <besideFl></besideFl>
        <router-view class="bodycontent"></router-view>
    </div>
  </div>
</template>

<script>
  import besideFl from '@/components/common/besideFl'
  import headColumn from '@/components/common/headColumn'
  export default {
    name: 'app',
    components: {
      headColumn:headColumn,
      besideFl:besideFl,
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .container{
    height:94%;
    overflow: hidden;
  }
  .bodycontent{
    float:left;
    width:90%;
    height:100%;
    background: #ececec;
  }
</style>



