<template>
	<div class="Statistics">
		<el-tabs type="border-card">
		  	<el-tab-pane>
		    	<span slot="label"><i class="icon iconfont icon-saomiao"></i> 页面登陆</span>
				<div id="main0" :style="{width:'1649px',height:'400px'}"></div>	    	
		  	</el-tab-pane>
		  	<el-tab-pane>
		    	<span slot="label"><i class="icon iconfont icon-fujian"></i> 页面转载</span>
		    	<div id="main1" :style="{width:'1649px',height:'400px'}"></div>	
		  	</el-tab-pane>
		  	<el-tab-pane>
		    	<span slot="label"><i class="icon iconfont icon-xihuan"></i> 页面关注</span>
		    	<div id="main2" :style="{width:'1649px',height:'400px'}"></div>	
		  	</el-tab-pane>
		  	<el-tab-pane>
		    	<span slot="label"><i class="icon iconfont icon-sheji-xianxing"></i> 页面编辑</span>
		    	<div id="main3" :style="{width:'1649px',height:'400px'}"></div>	
		  	</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
// echarts相关
let echarts = require('echarts/lib/echarts');

export default {
		data() {
	      	return {
      		}
    	},
    	mounted (){
    		var main0 = document.getElementById('main0');
    		var main1 = document.getElementById('main1');
    		var main2 = document.getElementById('main2');
    		var main3 = document.getElementById('main3');

			//用于使chart自适应高度和宽度,通过窗体高宽计算容器高宽
			var resizemain0 = function () {
    			var w0 = document.getElementsByClassName('Statistics')[0].children[0].clientWidth-30;
			    main0.style.width = w0+'px';
			    main1.style.width = w0+'px';
			    main2.style.width = w0+'px';
			    main3.style.width = w0+'px';
			};
			//设置容器宽
			resizemain0();
			// 基于准备好的dom，初始化echarts实例
			var myChart0 = echarts.init(main0);
			var myChart1 = echarts.init(main1);
			var myChart2 = echarts.init(main2);
			var myChart3 = echarts.init(main3);
			
			var option0 = {
			    tooltip : {
			        trigger: 'item'
			    },
			    toolbox: {
			        show : true,
			        feature : {
			            mark : {show: true},
			            dataZoom : {show: true},
			            dataView : {show: true, readOnly: false},
			            restore : {show: true},
			            saveAsImage : {show: true}
			        }
			    },
			    dataRange: {
			        min: 0,
			        max: 100,
			        y: 'center',
			        text:['高','低'],           // 文本，默认为数值文本
			        color:['lightgreen','yellow'],
			        calculable : true
			    },
			    xAxis : [
			        {
			            type : 'value',
			            scale : true
			        }
			    ],
			    yAxis : [
			        {
			            type : 'value',
			            position:'right',
			            scale : true
			        }
			    ],
			    animation: false,
			    series : [
			        {
			            name:'scatter1',
			            type:'scatter',
			            symbolSize:5,
			            data: (function () {
			                var d = [];
			                var len = 500;
			                var value;
			                while (len--) {
			                    value = (Math.random()*100).toFixed(2) - 0;
			                    d.push([
			                        (Math.random()*value + value).toFixed(2) - 0,
			                        (Math.random()*value).toFixed(2) - 0,
			                        value
			                    ]);
			                }
			                return d;
			            })()
			        }
			    ]
			};
			var option1 = {
			    color : [
			        'rgba(255, 69, 0, 0.5)',
			        'rgba(255, 150, 0, 0.5)',
			        'rgba(255, 200, 0, 0.5)',
			        'rgba(155, 200, 50, 0.5)',
			        'rgba(55, 200, 100, 0.5)'
			    ],
			    title : {
			        text: '页面转载分析',
			        subtext: '页面分析'
			    },
			    tooltip : {
			        trigger: 'item',
			        formatter: "{a} <br/>{b} : {c}%"
			    },
			    toolbox: {
			        show : true,
			        feature : {
			            mark : {show: true},
			            dataView : {show: true, readOnly: false},
			            restore : {show: true},
			            saveAsImage : {show: true}
			        }
			    },
			    legend: {
			        data : ['展现','点击','访问','咨询','订单']
			    },
			    calculable : true,
			    series : [
			        {
			            name:'预期',
			            type:'funnel',
			            x: '10%',
			            width: '80%',
			            itemStyle: {
			                normal: {
			                    label: {
			                        formatter: '{b}预期'
			                    },
			                    labelLine: {
			                        show : false
			                    }
			                },
			                emphasis: {
			                    label: {
			                        position:'inside',
			                        formatter: '{b}预期 : {c}%'
			                    }
			                }
			            },
			            data:[
			                {value:60, name:'访问'},
			                {value:40, name:'咨询'},
			                {value:20, name:'订单'},
			                {value:80, name:'点击'},
			                {value:100, name:'展现'}
			            ]
			        },
			        {
			            name:'实际',
			            type:'funnel',
			            x: '10%',
			            width: '80%',
			            maxSize: '80%',
			            itemStyle: {
			                normal: {
			                    borderColor: '#fff',
			                    borderWidth: 2,
			                    label: {
			                        position: 'inside',
			                        formatter: '{c}%',
			                        textStyle: {
			                            color: '#fff'
			                        }
			                    }
			                },
			                emphasis: {
			                    label: {
			                        position:'inside',
			                        formatter: '{b}实际 : {c}%'
			                    }
			                }
			            },
			            data:[
			                {value:30, name:'访问'},
			                {value:10, name:'咨询'},
			                {value:5, name:'订单'},
			                {value:50, name:'点击'},
			                {value:80, name:'展现'}
			            ]
			        }
			    ]
			};
			var labelRight = {normal: {label : {position: 'right'}}};
			var option2 = {
			    title: {
			        text: '关注与取消关注',
			        subtext: 'From ExcelHome',
			        sublink: 'http://e.weibo.com/1341556070/AjwF2AgQm'
			    },
			    tooltip : {
			        trigger: 'axis',
			        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
			            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
			        }
			    },
			    toolbox: {
			        show : true,
			        feature : {
			            mark : {show: true},
			            dataView : {show: true, readOnly: false},
			            magicType : {show: true, type: ['line', 'bar']},
			            restore : {show: true},
			            saveAsImage : {show: true}
			        }
			    },
			    grid: {
			        y: 80,
			        y2: 30
			    },
			    xAxis : [
			        {
			            type : 'value',
			            position: 'top',
			            splitLine: {lineStyle:{type:'dashed'}},
			        }
			    ],
			    yAxis : [
			        {
			            type : 'category',
			            axisLine: {show: false},
			            axisLabel: {show: false},
			            axisTick: {show: false},
			            splitLine: {show: false},
			            data : ['ten', 'nine', 'eight', 'seven', 'six', 'five', 'four', 'three', 'two', 'one']
			        }
			    ],
			    series : [
			        {
			            name:'生活费',
			            type:'bar',
			            stack: '总量',
			            itemStyle : { normal: {
			                color: 'orange',
			                borderRadius: 5,
			                label : {
			                    show: true,
			                    position: 'left',
			                    formatter: '{b}'
			                }
			            }},
			            data:[
			                {value:-0.07, itemStyle:labelRight},
			                {value:-0.09, itemStyle:labelRight},
			                0.2, 0.44, 
			                {value:-0.23, itemStyle:labelRight},
			                0.08,
			                {value:-0.17, itemStyle:labelRight},
			                0.47,
			                {value:-0.36, itemStyle:labelRight},
			                0.18
			            ]
			        }
			    ]
			};
			var option3 = {
			    title : {
			        text : '页面编辑分析',
			        subtext : '页面编辑查看'
			    },
			    tooltip : {
			        trigger: 'item',
			        formatter : function (params) {
			            var date = new Date(params.value[0]);
			            data = date.getFullYear() + '-'
			                   + (date.getMonth() + 1) + '-'
			                   + date.getDate() + ' '
			                   + date.getHours() + ':'
			                   + date.getMinutes();
			            return data + '<br/>'
			                   + params.value[1] + ', ' 
			                   + params.value[2];
			        }
			    },
			    toolbox: {
			        show : true,
			        feature : {
			            mark : {show: true},
			            dataView : {show: true, readOnly: false},
			            restore : {show: true},
			            saveAsImage : {show: true}
			        }
			    },
			    dataZoom: {
			        show: true,
			        start : 70
			    },
			    legend : {
			        data : ['series1']
			    },
			    grid: {
			        y2: 80
			    },
			    xAxis : [
			        {
			            type : 'time',
			            splitNumber:10
			        }
			    ],
			    yAxis : [
			        {
			            type : 'value'
			        }
			    ],
			    series : [
			        {
			            name: 'series1',
			            type: 'line',
			            showAllSymbol: true,
			            symbolSize: function (value){
			                return Math.round(value[2]/10) + 2;
			            },
			            data: (function () {
			                var d = [];
			                var len = 0;
			                var now = new Date();
			                var value;
			                while (len++ < 200) {
			                    d.push([
			                        new Date(2014, 9, 1, 0, len * 10000),
			                        (Math.random()*30).toFixed(2) - 0,
			                        (Math.random()*100).toFixed(2) - 0
			                    ]);
			                }
			                return d;
			            })()
			        }
			    ]
			};		 
			myChart0.setOption(option0);
			myChart1.setOption(option1);
			myChart2.setOption(option2);
			myChart3.setOption(option3);
			window.onresize = function () {
			    //重置容器高宽
			    resizemain0();
			    myChart0.resize();
			    myChart1.resize();
			    myChart2.resize();
			    myChart3.resize();
			}; 	
    	}
    }	      		
	
</script>

<style>
	.Statistics .el-tabs{
		width:98%;
		margin:25px auto;
	}
</style>


