<template>
	<div class="headcolumn">
		<img src="../../assets/logo1.png">
		<div class="headinfor">
			<el-autocomplete
			  v-model="state4"
			  :fetch-suggestions="querySearchAsync"
			  placeholder="请输入内容"
			  @select="handleSelect"
			  icon="edit">
			</el-autocomplete>
			<a class="help_infor"><i class="icon iconfont icon-guize"></i>帮助信息</a>
			<el-dropdown trigger="click">
			  	<span class="el-dropdown-link">
			    	信息查看<i class="el-icon-arrow-down el-icon--right"></i>
			  	</span>
			  	<el-dropdown-menu slot="dropdown">
            <el-dropdown-item class="clearfix">
              <el-button type="text" @click="openWeb">关于网站</el-button>
  			    </el-dropdown-item>
  			    <el-dropdown-item class="clearfix">
              <el-button type="text" @click="openAuthor">关于作者</el-button>
  			    </el-dropdown-item>
  			  </el-dropdown-menu>
			</el-dropdown>
		</div>
	</div>
</template>

<script>
    export default {
    data() {
      return {
        restaurants: [],
        state4: '',
        timeout:  null
      };
    },
    methods: {
      loadAll() {
        return [
          { "value": "www.cnblogs.com/momei/", "address": "博客" },
          { "value": "www.cnblogs.com/momei/", "address": "博客" },
          { "value": "www.cnblogs.com/momei/", "address": "博客" },
          { "value": "www.cnblogs.com/momei/", "address": "博客" },
          { "value": "www.cnblogs.com/momei/", "address": "博客" }
        ];
      },
      querySearchAsync(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 3000 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
      openWeb() {
        this.$alert('该后网站由Vue.js、element组件及ECharts混合开发完成。', '网站概述', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `网站信息概述！`
            });
          }
        });
      },
      openAuthor() {
        this.$alert('杨向辉：WEB前端、UI设计', '作者信息', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `作者信息概述！`
            });
          }
        });
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
  };
</script>
</script>

<style>
	.headcolumn {
		background: #20A0FF;
		overflow:hidden;
	}
	.headcolumn img{
		height: 40px;
	    padding: 5px 25px;
	    float: left;
	}
	.headinfor {
		float: right;
		padding:7px 15px;
	}
	.help_infor {
		margin: 0 35px 0 20px;
		color:#fff;
		font-size:14px;
		cursor:pointer;
	}
	.help_infor .icon {
		position:relative;
		top:2px;
		right:2px;
	}
	.el-dropdown {
		color:#fff;
	}
  .el-input__inner {
    background-color: rgba(255, 255, 255, 0.82);
  }
  .el-dropdown-link {
    cursor: pointer;
  }
</style>


